.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}/* Desaparecer el cursor en pantallas grandes */
@media (min-width: 768px) {
  body {
    cursor: none; /* Cursor inicial en pantallas grandes */
  }

  a {
    cursor: none; /* Cursor en los enlaces en pantallas grandes */
  }

  a:hover {
    cursor: none; /* Cursor en los enlaces al hacer hover en pantallas grandes */
  }
}

/* Cursor normal en pantallas móviles */
@media (max-width: 767px) {
  body, a, a:hover {
    cursor: auto; /* Cursor visible en dispositivos móviles */
  }
}

@media (max-width: 768px) {
  .hide-on-mobile {
    display: none;
  }
}